import React from 'react';
import { categories } from '../utils/constants';

const SideBar = ({ selectedCategory, setSelectedCategory }) => {
	return (
		<>
			<div className='offcanvas offcanvas-start d-flex flex-column justify-content-start bg-main text-white' data-bs-scroll='true' tabIndex='-1' id='offcanvasWithBothOptions' aria-labelledby='offcanvasWithBothOptionsLabel'>
				<button type='button' className='btn-close' data-bs-dismiss='offcanvas' aria-label='Close'></button>
				{categories.map((category) => (
					<button
						onClick={() => {
							setSelectedCategory(category.name);
						}}
						key={category.name}
						className='category-btn text-white w-100 p-2 mb-1 mb-1'
						on
						style={{ background: category.name === selectedCategory && 'red' }}
						data-bs-toggle='offcanvas'
						data-bs-target='#offcanvasWithBothOptions'
						aria-controls='offcanvasWithBothOptions'>
						<span className='me-4' style={{ color: category.name !== selectedCategory && 'red' }}>
							{category.icon}
						</span>
						<span style={{ opacity: category.name !== selectedCategory && '0.6' }}>{category.name}</span>
					</button>
				))}
				<h6 className='fs-6 py-2 border-top'>
					Made By <br /> Ibrahim-Omar
				</h6>
			</div>
		</>
	);
};

export default SideBar;
