import React, { useState } from 'react';
import SearchIcon from '@mui/icons-material/Search';
import { useNavigate } from 'react-router-dom';

const SearchBar = () => {
	const [searchTerm, setSearchTerm] = useState('');
	let navigate = useNavigate();

	function handleSearch(e) {
		e.preventDefault();
		navigate('/search/' + searchTerm);
		setSearchTerm('');
	}
	return (
		<form className='search-form m-auto my-2 position-relative' onSubmit={handleSearch}>
			<SearchIcon className=' position-absolute end-0 translate-middle bottom-0 text-white' />
			<input onChange={(e) => setSearchTerm(e.target.value)} className='search_bar form-control me-2 py-2 px-4 ' type='search' name='search_bar' placeholder='Search' value={searchTerm} />
		</form>
	);
};

export default SearchBar;
