import axios from 'axios';
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import { useParams } from 'react-router-dom';
import ReactPlayer from 'react-player';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import RelatedVideos from '../component/RelatedVideos';
import API_KEY from '../API_KEY';
import { ScrollRestoration } from 'react-router-dom';

const VideoDetail = () => {
	let params = useParams();

	const [videoDetails, setVideoDetails] = useState(null);
	const options = {
		method: 'GET',
		url: 'https://youtube-v31.p.rapidapi.com/videos',
		params: { part: 'contentDetails,snippet,statistics', id: params.id },
		headers: {
			'X-RapidAPI-Key': API_KEY,
			'X-RapidAPI-Host': 'youtube-v31.p.rapidapi.com',
		},
	};

	useEffect(() => {
		axios
			.request(options)
			.then(function (response) {
				setVideoDetails(response.data.items[0]);
			})
			.catch(function (error) {
				console.error(error);
			});
	}, [params.id]);

	return (
		<>
			<div className='container-fluid'>
				<div className='row py-5'>
					<div className='videoContainer col-xxl-10 col-xl-12 text-white mb-5 '>
						<ReactPlayer
							className='react-player'
							url={`https://www.youtube.com/watch?v=${params.id}`}
							controls
						/>
						<h6 className='h5 p-4  fw-bold '>
							{videoDetails?.snippet.title}
						</h6>
						<div className='d-flex justify-content-between px-3'>
							<div className='channel-details text ps-3 d-flex'>
								<img
									className='video-img rounded-circle'
									src='https://yt3.ggpht.com/ZAUGohPa0GCQWeGeclI_lCU-l_bSpf5wYARgbBZ6hkyt_mVPp_nGzTwW9LTosJjM6lY-eiy5Fg=s88-c-k-c0x00ffffff-no-rj'
									alt=''
								/>
								<p className='text-muted px-2 m-0 d-flex justify-content-start align-content-center '>
									<span className='me-1 fs-5'>
										{videoDetails?.snippet.channelTitle}
									</span>
									<span>
										<CheckCircleIcon className=' fs-5 text-white' />
									</span>
								</p>
							</div>

							<p className='text-muted px-2 m-0'>
								{videoDetails?.statistics.viewCount} Views
							</p>
						</div>
					</div>
					<div className='col-xxl-2 col-xl-12'>
						<p className='text-white pt-3  mb-4 px-4 h4'>
							Related <span className='text-danger'>Videos</span>
						</p>
						<div className='row'>
							<RelatedVideos />
						</div>
					</div>
				</div>
			</div>
			<ScrollRestoration />
		</>
	);
};

export default VideoDetail;
