import React from 'react';
import { Link, useParams } from 'react-router-dom';
import { logo } from '../utils/constants';
import SearchBar from './SearchBar';
import MenuIcon from '@mui/icons-material/Menu';

const Navbar = () => {
	let params = useParams();
	return (
		<nav className='navbar navbar-expand-lg bg-main navbar-dark fixed-top shadow'>
			<div className='container-fluid'>
				{params.id || params.searchTerm ? '' : <MenuIcon className='burge text-white fs-1' data-bs-toggle='offcanvas' data-bs-target='#offcanvasWithBothOptions' aria-controls='offcanvasWithBothOptions' />}
				<div className=''>
					<Link to={'/'} className='navbar-brand ' href='#'>
						<img src={logo} alt='Logo' height={45} />
					</Link>
				</div>
				<SearchBar />
			</div>
		</nav>
	);
};

export default Navbar;
