import axios from 'axios';
import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { Link, useParams } from 'react-router-dom';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import API_KEY from '../API_KEY';

const RelatedVideos = () => {
	let { id } = useParams();

	const [video, setVideo] = useState([]);
	const options = {
		method: 'GET',
		url: 'https://youtube-v31.p.rapidapi.com/search',
		params: {
			relatedToVideoId: id,
			part: 'id,snippet',
			type: 'video',
			maxResults: '50',
		},
		headers: {
			'X-RapidAPI-Key': API_KEY,
			'X-RapidAPI-Host': 'youtube-v31.p.rapidapi.com',
		},
	};

	useEffect(() => {
		axios
			.request(options)
			.then(function (response) {
				setVideo(response.data.items);
			})
			.catch(function (error) {
				console.error(error);
			});
	}, [video]);
	return (
		<>
			{video.splice(0, 10).map((video, index) => (
				<div className='col-xxl-12 col-xl-4 col-md-6 '>
					<Link
						to={`/video/${video.id.videoId}`}
						className='video-card card bg-main  border-0 rounded-5  overflow-hidden'>
						<div className='thumbnails rounded-4'>
							<img
								src={
									video.snippet.thumbnails.high.url !== null
										? video.snippet.thumbnails.high.url
										: ''
								}
								className='card-img-top w-100'
								alt='...'
							/>
						</div>
						<div className='card-body m-0 ps-0 mb-0 mb-0 d-flex  justify-content-start gap-2 '>
							<img
								className='video-img rounded-circle'
								src='https://yt3.ggpht.com/ZAUGohPa0GCQWeGeclI_lCU-l_bSpf5wYARgbBZ6hkyt_mVPp_nGzTwW9LTosJjM6lY-eiy5Fg=s88-c-k-c0x00ffffff-no-rj'
								alt=''
							/>
							<div className='text  p-0'>
								<h6 className=' text-white '>{video.snippet.title}</h6>
								<p className='m-0 d-flex justify-content-start align-content-center text-muted'>
									<span className='me-1 h6'>
										{video.snippet.channelTitle}
									</span>
									<span>
										<CheckCircleIcon className='text-white' />
									</span>
								</p>
							</div>
						</div>
					</Link>
				</div>
			))}
		</>
	);
};

export default RelatedVideos;
