import React from 'react';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { Link } from 'react-router-dom';

const Videos = ({ videos }) => {
	const notfound = 'https://howfix.net/wp-content/uploads/2018/02/sIaRmaFSMfrw8QJIBAa8mA-article.png';
	return (
		<>
			{videos.map((video, index) => (
				<div key={index} className='col-xxl-3 col-lg-4 col-md-6  '>
					<Link to={`/video/${video.id.videoId}`} className='video-card card bg-main  border-0 rounded-5  overflow-hidden'>
						<div className='thumbnails rounded-4'>
							<img src={video.snippet.thumbnails.high.url !== null ? video.snippet.thumbnails.high.url : notfound} className='card-img-top w-100' alt='...' />
						</div>
						<div className='card-body m-0 ps-0 mb-0 mb-0 d-flex  justify-content-start gap-2 '>
							<img className='video-img rounded-circle' src='https://yt3.ggpht.com/ZAUGohPa0GCQWeGeclI_lCU-l_bSpf5wYARgbBZ6hkyt_mVPp_nGzTwW9LTosJjM6lY-eiy5Fg=s88-c-k-c0x00ffffff-no-rj' alt='' />
							<div className='text  p-0'>
								<h6 className='card-title fw-semibold  text-white'>{video.snippet.title}</h6>
								<p className='m-0 d-flex justify-content-start align-content-center text-muted'>
									<span className='me-1 fs-5'> {video.snippet.channelTitle}</span>
									<span>
										<CheckCircleIcon className=' fs-5 text-white' />
									</span>
								</p>
							</div>
						</div>
					</Link>
				</div>
			))}
		</>
	);
};

export default Videos;
